:root {
  --bcg-color1: #1e1e24;
  --bcg-color2: #1f1e1b;
  --acc-color1: #a35709;
  --acc-color2: #ff8303;
  --font-color1: #f0e3ca;
  --font-color2: rgba(240, 227, 202, .64);
}

.container {
  width: 100%;
  padding-left: 48px;
  padding-right: 48px;
}

.filter {
  align-items: center;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.filter_input {
  display: none;
}

.filter_input:checked + label {
  border-bottom: 2px solid var(--acc-color1);
  color: var(--font-color1);
  cursor: default;
}

.filter_label {
  cursor: pointer;
  margin: 0 10px;
  font-size: 20px;
  font-weight: 300;
  display: inline-block;
}

.filter_label:hover {
  border-bottom: 2px solid var(--acc-color2);
}

.notes_listWrapper {
  margin-bottom: 40px;
}

.notes_table {
  border-spacing: 0 20px;
  color: var(--font-color1);
  width: 100%;
  margin-top: 20px;
  font-size: 18px;
}

.notes_table th, .notes_table td {
  background-color: var(--bcg-color2);
  text-align: center;
  padding: 10px;
}

.notes_table th {
  background-color: var(--acc-color1);
  font-weight: bold;
}

tr.editable {
  outline: 1px solid var(--acc-color1);
}

.notes_table select {
  color: var(--font-color1);
  text-align: center;
  border: none;
  border-bottom: 1px solid var(--font-color2);
  background-color: rgba(0, 0, 0, 0);
  outline: none;
  width: 100%;
  font-size: 20px;
}

.notes_table input[type="text"] {
  color: var(--font-color1);
  text-align: center;
  border: none;
  border-bottom: 1px solid var(--font-color2);
  background-color: rgba(0, 0, 0, 0);
  outline: none;
  width: 100%;
  font-size: 20px;
}

.notes_table input[readonly], .notes_table select:disabled {
  border: none;
  font-size: 18px;
  text-decoration: none;
}

.notes_table select {
  -webkit-appearance: none;
  appearance: none;
  opacity: 1;
  background: none;
  width: 150px;
}

.notes_table button {
  background-color: var(--acc-color1);
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin-right: 5px;
  padding: 5px 10px;
}

.notes_table button.notes_editButton.editable {
  background-color: var(--acc-color2);
}

.notes_archiveAllButton, .notes_deleteAllButton {
  background-color: var(--acc-color1);
  color: var(--font-color1);
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin-right: 10px;
  padding: 5px 10px;
}

.addNoteButton {
  background-color: var(--acc-color1);
  color: var(--font-color1);
  cursor: pointer;
  border: none;
  border-radius: 5px;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 18px;
}

.addNoteButton:hover {
  background-color: var(--acc-color2);
  color: var(--bcg-color1);
}

.notes_editButton, .notes_archiveButton, .notes_deleteButton, .notes_archiveAllButton, .notes_deleteAllButton {
  width: 40px;
  height: 40px;
}

.notes_editButton {
  background: url("icon_edit.5683a5f1.svg") center no-repeat;
}

.notes_archiveButton, .notes_archiveAllButton {
  background: url("icon_archive.d29b620b.svg") center no-repeat;
}

.notes_deleteButton, .notes_deleteAllButton {
  background: url("icon_delete.7108857f.svg") center no-repeat;
}

.notes_category_icon {
  background-position: center;
  background-repeat: no-repeat;
  background-size: 50px;
  width: 80px;
}

.notes_category_icon.Task {
  background-image: url("icon_task.d9d9f6be.svg");
}

.notes_category_icon.Idea {
  background-image: url("icon_idea.d87d516f.svg");
  background-size: 40px;
}

.notes_category_icon.Random {
  background-image: url("icon_other.3a3d3bbf.svg");
}

.category_table {
  border-spacing: 0 20px;
  color: var(--font-color1);
  width: 100%;
  margin-top: 20px;
  font-size: 18px;
}

.category_table tr {
  height: 60px;
}

.category_table th, .category_table td {
  background-color: var(--bcg-color2);
  text-align: center;
  padding: 10px;
}

.category_table th {
  background-color: var(--acc-color1);
  font-weight: bold;
}

@media screen and (min-width: 320px) {
  html, body {
    overflow-x: hidden;
  }
}

body {
  background-color: var(--bcg-color1);
  color: var(--font-color2);
}

button {
  cursor: pointer;
}

button:hover {
  background-color: var(--acc-color2);
}

.visually-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

