// IMPORTS
@import './sass/vars.scss';
@import './sass/container.scss';
@import './partials/filter/filter';
@import './partials/notesList/notesList';
@import './partials/categoryList/categoryList';

// BASE STYLES
@media screen and (min-width: 320px) {
  html,
  body {
    overflow-x: hidden;
  }
}

body {
  background-color: var(--bcg-color1);
  color: var(--font-color2);
}

button {
  cursor: pointer;
  &:hover {
    background-color: var(--acc-color2);
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
